.App {
    background-color: #dfbdbd;
    color: rgb(16, 16, 56);

    border-radius: 10px;
    padding: 20px 20px 3px 20px;

    margin: 40px;

    
}

body{
    background-color: rgb(41, 48, 48);
    font-family: 'Inter', sans-serif;
    padding:20px;

}

html, body {
    height: 100%;
    margin: 0;
    padding: 0;
}

.flip-image{
    margin-top: 10px;
    max-width: 100%;
    max-height: 80vh;
    border-radius: 5px;
    margin-bottom: 0px;
    
}

.flipgame{
    display: flex;
 
    align-items: center;
    justify-content: center;
    flex-direction: column;

    text-align: center;
}

.flipgame button{
    font-family: 'Inter', sans-serif;
    background: #4D5B9E;
    border-radius: 15px;
    color: white;
    border: none;
    padding: 5px;
}

.surprise{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.surprise p{
    margin: 0px;
}